import React from "react"
import axios from "axios"
import {
  Button,
  Card,
  CardBody,
  CardText,
  Container,
  Spinner
} from "reactstrap"

class App extends React.Component {
  state = {}

  async componentDidMount() {
    await this.getJoke()
  }

  render() {
    return (
      <div className="bg-primary" style={{ width: "100vw", height: "100vh" }}>
        <Container
          className="d-flex flex-column justify-content-center align-items-center"
          style={{ height: "80%" }}
        >
          <Card
            className="shadow"
            style={{
              width: "60vw",
              minWidth: 300,
              maxWidth: 500,
              minHeight: 200
            }}
          >
            <CardBody className="d-flex flex-column justify-content-between align-items-center">
              {this.state.joke ? (
                <CardText
                  dangerouslySetInnerHTML={{ __html: this.state.joke }}
                  className="align-self-start"
                />
              ) : (
                <Spinner color="primary" style={{ margin: "auto" }} />
              )}
              <Button onClick={this.getJoke} color="primary">
                Give Me Another!
              </Button>
            </CardBody>
          </Card>
        </Container>
      </div>
    )
  }

  getJoke = async () => {
    const joke = await axios
      .get("https://icanhazdadjoke.com", {
        headers: {
          Accept: "application/json",
          "User-Agent": "Blues Angel Marketing (eric@bluesangelmusic.com)"
        }
      })
      .then(res => {
        return res.data.joke.replace(
          /(.*[.|?|!|"])\s+([\w|"].*)/gi,
          `<p>$1</p><p>$2</p>`
        )
      })
    this.setState({ joke })
  }
}

export default App
